"use client"

import axios from "axios"
import { useState } from "react"

// Função para verificar se está no browser
const isBrowser = () => typeof window !== "undefined"

// Não precisamos reescrever todo o arquivo, apenas a função refreshAuth

// Mock user for demonstration purposes.  Replace with actual user data and logic.
const mockUser = {
  id: 1,
  name: "Test User",
  email: "test@example.com",
  permissoes: ["read", "write"],
  perfil: "User",
  token: "initial_token",
}

// Função para atualizar a autenticação
const checkAuth = async (): Promise<boolean> => {
  if (!isBrowser()) return false

  const token = localStorage.getItem("token")
  return !!token
}

// Função para atualizar o token
const refreshAuth = async (): Promise<boolean> => {
  try {
    if (!isBrowser()) return false

    const token = localStorage.getItem("token")
    if (!token) {
      console.warn("Não há token para atualizar")
      return false
    }

    // Usar axios diretamente para evitar loops
    const baseUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8080/sistema-gestao"
    const response = await axios({
      method: "get",
      url: `${baseUrl}/api/auth/refresh`,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })

    if (response.data && response.data.token) {
      localStorage.setItem("token", response.data.token)

      // Atualizar o token no objeto do usuário
      if (user) {
        const updatedUser = { ...user, token: response.data.token }
        setUser(updatedUser)
        localStorage.setItem("user", JSON.stringify(updatedUser))
      }

      return true
    }

    return false
  } catch (error) {
    console.error("Erro ao atualizar token:", error)
    return false
  }
}

// Mock login and logout functions.  Replace with actual authentication logic.
const login = async () => {
  // Simulate successful login
  const newToken = "new_token"
  localStorage.setItem("token", newToken)
  setUser({ ...mockUser, token: newToken })
  localStorage.setItem("user", JSON.stringify({ ...mockUser, token: newToken }))
  setLoading(false)
}

const logout = async () => {
  localStorage.removeItem("token")
  localStorage.removeItem("user")
  setUser(null)
  setLoading(false)
}

const getInitialUser = () => {
  if (isBrowser()) {
    const storedUser = localStorage.getItem("user")
    return storedUser ? JSON.parse(storedUser) : null
  }
  return null
}

const [user, setUser] = useState(getInitialUser())
const [loading, setLoading] = useState(false)

// Adicionar refreshAuth ao value
const value = {
  user,
  loading,
  isAuthenticated: !!user,
  login,
  logout,
  checkAuth,
  refreshAuth,
  hasPermission: (permission: string) => {
    if (!user || !user.permissoes) return false
    return user.permissoes.includes(permission) || user.perfil === "Administrador" || user.perfil === "ADMIN"
  },
}
